<template>
  <v-app>
    <v-app-bar app absolute color="#fcb69f" dark src="https://picsum.photos/1920/1080?random">
      <template v-slot:img="{ props }">
        <v-img v-bind="props" gradient="to top right, rgba(19,84,122,.5), rgba(128,208,199,.8)"></v-img>
      </template>

      <v-toolbar-title>Posts</v-toolbar-title>
    </v-app-bar>

    <v-main>
      <v-container>
        <form @submit.prevent="addPost">
          <v-text-field class="pa-2" v-model="title" clearable label="Title" append-icon="mdi-plus"></v-text-field>
          <v-textarea class="pa-2" v-model="description" clearable label="Description" rows="2" append-icon="mdi-comment"></v-textarea>
          <v-btn class="mr-4" type="submit">
            submit
          </v-btn>
        </form>
        <v-list flat>
          <div v-for="(post, index) in posts" :key="index">
            <v-list-item>
              <template v-slot:default>
                <v-list-item-action>
                  <!-- :input-value="post.done" -->
                  <v-checkbox></v-checkbox>
                </v-list-item-action>

                <v-list-item-content>
                  <v-list-item-title>{{ post.title }}</v-list-item-title>
                  <v-list-item-subtitle>{{ post.description }}</v-list-item-subtitle>
                </v-list-item-content>
                <v-list-item-action>
                  <v-btn icon @click="deletePost(post._id)" hint="Delete">
                    <v-icon color="grey lighten-1">mdi-delete</v-icon>
                  </v-btn>
                  <v-btn icon dark @click.stop="open(post)" hint="Edit">
                    <v-icon color="grey lighten-1">mdi-pencil</v-icon>
                  </v-btn>
                </v-list-item-action>
              </template>
            </v-list-item>
            <v-divider></v-divider>
          </div>
        </v-list>
        <v-dialog v-model="dialog" max-width="400">
          <v-card>
            <v-card-title>
              <span class="headline">Edit Post</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field label="Title" required v-model="post.title"></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field label="Description" v-model="post.description"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>

              <v-btn color="green darken-1" text @click="dialog = false">
                Close
              </v-btn>

              <v-btn color="green darken-1" text @click="save()">
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import axios from "axios";

export default {
  name: "App",

  data: () => ({
    dialog: false,
    title: "",
    description: "",
    posts: [],
    post: {},
  }),

  mounted() {
    this.load();
  },

  methods: {
    async load() {
      const res = await axios.get("api/post");
      this.posts = res.data;
    },

    async addPost() {
      await axios.post("api/post", {
        title: this.title,
        description: this.description,
      });

      this.title = "";
      this.description = "";

      this.load();
    },

    async deletePost(id) {
      this.posts = this.posts.filter((post) => post._id !== id);
      await axios.delete(`api/post/${id}`);
    },

    open(post) {
      this.dialog = true;
      this.post = post;
    },

    async save() {
      axios.put(`api/post/${this.post._id}`, {
        set: {
          title: this.post.title,
          description: this.post.description,
        },
      });

      this.dialog = false;
    },
  },
};
</script>
